nav{
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    button{
        background: transparent;
        border: 2px solid rgb(65, 65, 65) ;
        cursor: pointer;
        padding : 1rem;
        border-radius:0.5rem ;
        transition: all 0.3s ease;
        &:hover{
            background-color: rgb(65, 65, 65);
            color: white;
        }
    }
}

@media screen and (max-width: 768px) {
    nav{
        button{
            z-index: 60;
        }
    }
}